@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  font-family: Russo One; }

#root {
  display: flex;
  justify-content: center;
  align-items: center; }

.cover-container {
  max-width: 500px; }
  .cover-container .pad-bank .drum-pad {
    min-width: 100px;
    min-height: 80px;
    cursor: pointer; }
  .cover-container .logo-block {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: #17a2b8; }
    .cover-container .logo-block .logo {
      line-height: 1.3;
      margin-right: 5px; }
  .cover-container .display {
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: #6c757d; }
  .cover-container .custom-range::-webkit-slider-thumb {
    background: #17a2b8; }
  .cover-container .custom-control-input:checked ~ .custom-control-label::before {
    border-color: #17a2b8;
    background-color: #17a2b8; }
  .cover-container .custom-control-label {
    color: #6c757d;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }

@media screen and (max-width: 650px) {
  .cover-container {
    -webkit-transform: scale(0.7);
            transform: scale(0.7); } }

